/* side navbar */
.navbar {
    width: 5vmax;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    position: fixed;
    top: 0%;
    left: 0%;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.06);
}

.navbar .logo>img {
    width: 38.1px;
    height: 36.5px;
    margin: 15px;
    object-fit: contain;
}

.navLinkDiv {
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
}

.navLinkDiv>div:first-child>img {
    width: 30px;
    height: 30px;
    margin: 15px 0;
    object-fit: contain;
}
.navLinkDiv>div:last-child>img {
    width: 18.5px;
    height: 18px;
    margin: 15px 0;
    object-fit: contain;
}

.navlink {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

 /* Top navbar */

.topnav{
    display:flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 5vmax;
    width: auto;
    background-color: #01896a;
    padding: 10px 15px;
}
.toggle_btn{
    border-radius: 20px;
    box-shadow: 0 2px 0 0 #009d79, inset 0 2px 0 0 #006c53;
    background-color: #00775c;
    padding: 11px 2px ;
    position: relative;
    width: 170px;
    height: 40px;
}
.toggle_btn button{
    width: 80px;
    height: 34px;
    position: absolute;
    top: 3px;
    transition: all 0.5s;
    z-index: 1;
    border-radius: 17px;
    box-shadow: 0 2px 0 0 #006c53, inset 0 2px 0 0 #009d79;
    background-color: #009372;
    border: none;
    outline: none;
}
.toggle_btn span:first-child{
    
    padding: 9px 14px 9px 17px;
    color: white;
    z-index: 3;
    position: absolute;
    top: 3px;
    left: 2px;
}
.toggle_btn span:nth-child(2){
    padding: 9px 14px 9px 17px;
    border-radius: 17px;
    color: white;
    position: absolute;
    top: 3px;
    right: 2px;
    z-index: 2;
}
.left{
    transform: translateX(1px);
    font-weight: bolder;
}
.right{
    transform: translateX(85px);
    font-weight: bolder;
}
.searchBox{
    position: relative;
}
.searchBox>input{
    width: 28vmax;
    padding: 12px 15.2px 12px 30.2px;
    border-radius: 20px;
    background-color: #fff;
    border: none;
    outline: none;
}
.searchBox>img{
    position: absolute;
    top: 11px;
    left: 9px;
}
.notification{
    display: flex;
    justify-content: center;
    align-items: center;
}
.notification>div{
    margin: 0 8px;
}
.profile{
    margin: 2px 0 0 0 !important;
    width: 50px;
    height: 50px;
}
.profile>a>img{
    width: 100%;
}
.mess{
    position: relative;
}
.mess>div:last-child{
width: 15px;
height: 15px;
border-radius: 100%;
background-color: red;
position: absolute;
top: -5px;
right: -5px;
font-size: 10px;
color: white;
display: flex;
align-items: center;
font-weight: 600;
justify-content: center;
}