.login {
    width: auto;
    height: 90vh;
    margin-left: 5vmax;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f2f2f2;

}

.login>form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #009d79;
    padding: 20px;
    color: #fff;
}
.login>form>input[type="email"],
.login>form>input[type="password"] {

    border: 1px solid rgba(66, 66, 66, 0.616);
    border-radius: 10px;
    margin: 10px 0;
    padding: 20px;
    box-shadow: 0 2px 0 0 #009d79, inset 0 2px 0 0 #006c53;
    background-color: #00775c;
    outline: none;
    color: white;
}
.login>form>input[type="email"]::placeholder,
.login>form>input[type="password"]::placeholder{
color: white;
}
.login>form>input[type='submit']{
    padding: 10px;
    border: none;
    outline: none;
    background-color: #08bb94;
    font-weight: bold;
    color: white;
    border-radius: 5px ;
    transition: all 0.5s;
}
.login>form>input[type='submit']:hover{
    box-shadow: inset 0 0 0 20px #006c53;
}