.pg{
    height: 15vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.pg ul{
    margin-left: 5vmax;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 16px;
    font-weight: 500;
    color: #2a2a2e;
    width: 93vmax;
    overflow-x: auto;
}
.pg ul::-webkit-scrollbar-thumb{
    background-color: transparent;
}
.pg li{
    padding: 12px 19px 11px 19px;
    border-radius: 6px;
    border: solid 1.5px #ebebeb;
    background-color: #fff;
    margin: 20px 0 20px 20px;
    list-style: none;
}

.pg ul a {
    text-decoration: none;
    color: black;
}
.pg ul a li:hover{
color: white;
    background-color: #01896a !important;
}
.pg ul a:first-child li{
    color: white;
    background-color: #01896a !important;
}
