.dashboard {
    width: auto;
    margin-left: 5vmax;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 10px 15px;
    background-color: #f2f2f2;
}

.dashcard {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    width: 24%;
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.08);
    background-color: #fff;
    margin: 10px 5px 10px 5px;

}

.banner {
    width: 100%;
    flex-wrap: nowrap;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 4px;
}
.banner>div:first-child>img{
    border-top-left-radius: 4px;
    border-bottom-left-radius:4px ;
}
.banner>div:last-child>img{
    border-top-right-radius: 4px;
    border-bottom-right-radius:4px ;
}
.banner>div{
    margin: 0 5px;
}
.banner>div>img {
   width: 100%;
   height: 90px;
   object-fit: cover;
}

.infos{
    position: relative;
    padding-top: 50px;
}
.infos>.logo{
    position: absolute;
    top: -55px;
    left: 5px;
}
.infos>.details{
    margin-left: 15px;
}

.infos>.details>h2{
    font-size: 20px;
    font-weight: bold;
    text-align: left;
    color: #2a2a2e;
}
.infos>.details>.detail{
    font-size: 14px;
    color: #63636a;
   
}
.infos>.details>.detail>div{
    margin: 8px 0;
}
.infos>.details>.detail>.address{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.infos>.details>.detail>.address>span{
    font-size: 14px;
    font-weight: 600;
    color: #01896a;
}
.dot{
    width: 3px;
    height: 3px;
    border-radius: 100%;
    background-color: gray;
    margin: 0 5px;
}
.infos>.details>button{
    background-color: #d1ffd0;
    border: none;
    outline: none;
    padding: 8px 20px 8px 22px;
    border-radius: 16px;
    color: #01896a;
    font-weight: 600;
  
}